import http from './http-common';
import { token as Token } from '../helpers';

const authenticationDataService = {
    create: (data) => http.post('/token/create/', data),
    verify: (data) => http.post('/token/verify/', data),
    refresh: (data) => http.post('/token/refresh/', data),
    blacklist: (data) => http.post('/token/blacklist/', data),
    getUserInfo: () => http.get('/accounts/me/', getOptions())
}

function getOptions() {
    let options = {};
    if(Token.tokenAccessIsValid()) {
        let token = Token.getToken();
        options.headers = { 
            Authoriztion: 'Bearer ' + token.access,
            "Content-type": "application/json" 
        };
    }
    return options;
}

export { authenticationDataService };