import React, { useState, useEffect } from 'react';
import { useSelector } from 'react-redux';
import { 
    Spin, 
    PageHeader, 
    Button,
    Tag,
} from 'antd';
import { 
    ReferenceTable, 
    ReferenceForm, 
} from '../components';

const Furniture = ({
    items,
    isFetching,
    fetchAllFurniture,
    createFurniture,
    editFurniture,
    deleteFurniture,
}) => {

    const furnitureTypeOptions = useSelector(
        state => state.furnitureTypes.items).map(item => (
            {value: item.id, text: item.name}
    ));
    
    const furnitureColorOptions = useSelector(
        state => state.furnitureColors.items).map(item => (
            {value: item.id, text: item.name, code: item.code}
    ));

    const furnitureMaterialOptions = useSelector(
        state => state.furnitureMaterials.items).map(item => (
            {value: item.id, text: item.name}
    ));

    const furniturePublishOptions = [
        {value: true, text: 'Published'},
        {value: false, text: 'Pending'},
    ];

    const dataSource = items.map((item, index) => {
        return Object.assign({}, item, { key: item.id, no: index + 1});
    });

    const [visibleForm, setVisibleForm] = useState(false);
    const [actionForm, setActionForm] = useState({action: '', data: {}});
    const onSubmitForm = ({action, result}) => {
        setVisibleForm(false);
        if(action.type === 'create') {
            createFurniture(result)
        } else if(action.type === 'update') {
            editFurniture(action.data.id, result);
        }
    };

    const columns = [
        {
            title: 'No',
            dataIndex: 'no',
            width: '5%'
        },
        {
            title: 'Address in Unity',
            dataIndex: 'id',
            render: id => (
                <span>{`fur_${id}`}</span>
            ),
            hide: true
        },
        {
            title: 'Name',
            dataIndex: 'name',
            editable: true,
            inputType: 'text',
            width: '25%',
            sorter: (a, b) => a.name.localeCompare(b.name)
        },
        {
            title: 'Description',
            dataIndex: 'description',
            editable: true,
            inputType: 'textArea',
            hide: true
        },
        {
            title: 'Width',
            dataIndex: 'width',
            editable: true,
            inputType: 'number',
            hide: true,
            render: width => (
                <span>
                    {
                        width.toLocaleString('it-IT', 
                        {style : 'unit', unit : 'centimeter', unitDisplay: 'short'})
                    }
                </span>
            )
        },
        {
            title: 'Height',
            dataIndex: 'height',
            editable: true,
            inputType: 'number',
            hide: true,
            render: height => (
                <span>
                    {
                        height.toLocaleString('it-IT', 
                        {style : 'unit', unit : 'centimeter', unitDisplay: 'short'})
                    }
                </span>
            )
        },
        {
            title: 'Depth',
            dataIndex: 'depth',
            editable: true,
            inputType: 'number',
            hide: true,
            render: depth => (
                <span>
                    {
                        depth.toLocaleString('it-IT', 
                        {style : 'unit', unit : 'centimeter', unitDisplay: 'short'})
                    }
                </span>
            )
        },
        {
            title: 'Price',
            dataIndex: 'price',
            editable: true,
            inputType: 'number',
            width: '15%',
            sorter: (a, b) => a.number - b.number,
            render: price => (
                <span>{price.toLocaleString('it-IT', {style : 'currency', currency : 'VND'})}</span>
            )
        },
        {
            title: 'Type',
            dataIndex: 'furniture_type',
            editable: true,
            selectable: true,
            options: furnitureTypeOptions,
            width: '20%',
            filters: furnitureTypeOptions,
            onFilter: (value, record) => record.furniture_type === value,
        },
        {
            title: 'Colors',
            dataIndex: 'color',
            editable: true,
            selectable: true,
            mode: 'multiple',
            options: furnitureColorOptions,
            hide: true,
            render: color => (
                furnitureColorOptions.filter(option => 
                    color.includes(option.value)).map((option, index) => 
                        <Tag 
                            key={index} 
                            color={option.code}
                            style={{fontSize: '1em'}}
                        >
                            {option.text}
                        </Tag>
                )
            )
        },
        {
            title: 'Materials',
            dataIndex: 'material',
            editable: true,
            selectable: true,
            mode: 'multiple',
            options: furnitureMaterialOptions,
            hide: true,
            render: material => (
                furnitureMaterialOptions.filter(option => 
                    material.includes(option.value)).map((option, index) => 
                        <Tag key={index} style={{fontSize: '1em'}} >{option.text}</Tag>
                )
            )
        },
        {
            title: 'Status',
            dataIndex: 'is_publish',
            editable: true,
            selectable: true,
            options: furniturePublishOptions,
            width: '15%',
            filters: furniturePublishOptions,
            onFilter: (value, record) => record.is_publish === value,
        },
    ];

    useEffect(() => {
        fetchAllFurniture();
    // eslint-disable-next-line
    }, []);

    return (
        <Spin spinning={isFetching}>
            
            <ReferenceForm 
                title={'Furniture'}
                visible={visibleForm}
                columns={columns}
                onSubmit={onSubmitForm}
                onCancel={() => setVisibleForm(false)}
                action={actionForm}
            />
            <PageHeader title={'Furnitures'}>
                <Button type="primary" onClick={() => {
                    setActionForm({type: 'create', data: {is_publish: false}})
                    setVisibleForm(true);
                }}
                >New</Button>
            </PageHeader>
            <ReferenceTable 
                dataSource={dataSource} 
                columns={columns.filter(col => !col.hide)} 
                style={{
                    margin: '0 24px'
                }}
                onHandleEdit={(record) => {
                    setActionForm({type: 'update', data: record})
                    setVisibleForm(true);
                }}
                onHandleDetail={(record) => {
                    setActionForm({type: 'display', data: record})
                    setVisibleForm(true);
                }}
                onHandleDelete={(record) => {
                    deleteFurniture(record.id);
                }}
            />
        </Spin>
    );
};

export { Furniture };