import { combineReducers } from 'redux';
import { authentication } from './authentication.reducer';
import { roomType as roomTypes } from './room-type.reducer';
import { furnitureType as furnitureTypes } from './furniture-type.reducer';
import { furnitureColor as furnitureColors } from './furniture-color.reducer';
import { furnitureMaterial as furnitureMaterials } from './furniture-material.reducer';
import { furniture as furnitures } from './furniture.reducer';
import { version as versions } from './version.reducer';

const rootReducer = combineReducers({
    authentication,
    roomTypes,
    furnitureTypes,
    furnitureColors,
    furnitureMaterials,
    furnitures,
    versions
});

export default rootReducer;