import React, { useState } from 'react';
import { Layout, Menu, Popconfirm, Divider } from 'antd';
import {
    QuestionCircleOutlined,
    //TranslationOutlined,
    LogoutOutlined,
    UploadOutlined,
    FormatPainterOutlined,
    SkinOutlined,
    GroupOutlined,
    DashboardOutlined,
    ProfileOutlined,
    RocketOutlined,
    BuildOutlined
} from '@ant-design/icons';
import { Link, Switch, useLocation } from 'react-router-dom';
import { 
    RoomType, 
    FurnitureType,
    FurnitureColor,
    FurnitureMaterial,
    Furniture,
    Version,
} from '../containers';
import { PrivateRoute } from './private-route.component';
import { Addressable } from './addressable.component';
import { Home } from './home.component';
const { Header, Footer, Sider, Content } = Layout;

const privateRoutes = [
    {
      path: '/',
      exact: true,
      name: 'Home',
      sidebar: 'Home',
      icon: <DashboardOutlined />,
      main: () => <Home />
    },
    {
        path: '/versions/',
        exact: true,
        name: 'Versions',
        sidebar: 'Versions',
        icon: <RocketOutlined />,
        main: () => <Version />
    },
    {
        path: '/addressables/',
        exact: true,
        name: 'Addressables',
        sidebar: 'Addressables',
        icon: <UploadOutlined />,
        main: () => <Addressable />
    },
    {
        path: '/furnitures/',
        exact: true,
        name: 'Furnitures',
        sidebar: 'Furnitures',
        icon: <BuildOutlined />,
        main: () => <Furniture />
    },
    {
      path: '/furniture-types/',
      exact: true,
      name: 'Furniture Types',
      sidebar: 'Furniture Types',
      icon: <ProfileOutlined />,
      main: () => <FurnitureType />
    },
    {
        path: '/furniture-colors/',
        exact: true,
        name: 'Furniture Colors',
        sidebar: 'Furniture Colors',
        icon: <FormatPainterOutlined />,
        main: () => <FurnitureColor />
    },
    {
        path: '/furniture-materials/',
        exact: true,
        name: 'Furniture Materials',
        sidebar: 'Furniture Materials',
        icon: <SkinOutlined />,
        main: () => <FurnitureMaterial />
    },
    {
        path: '/room-types/',
        exact: true,
        name: 'Room Types',
        sidebar: 'Room Types',
        icon: <GroupOutlined />,
        main: () => <RoomType />
    },
];

const Logout = ({logout, children}) => (
    <Popconfirm
        placement="bottomRight"
        onConfirm={() => {logout()}}
        title="Are you sure to logout?"
        okText="Yes"
        cancelText="No"
        icon={<QuestionCircleOutlined style={{color: '#ff0000'}} />}
    >{children}</Popconfirm>
)

const MainLayout = ({logout}) => {
    
    const [collapsed, setCollapsed] = useState(false);
    const onCollapse = (collapsed) => {
        setCollapsed(collapsed);
    };

    let location = useLocation();
    const menuItems = privateRoutes.map((route) => {
        return (
            <Menu.Item key={route.path}
                style={{margin: 0}}
            >
                <Link to={route.path}>
                {route.icon}
                <span>{route.sidebar}</span>
                </Link>
            </Menu.Item>
        )
    });

    const childrens = privateRoutes.map((route, index) => (
        <PrivateRoute 
            key={index} 
            exact={route.exact} 
            path={route.path} 
            component={route.main} 
        />
    ));

    return (
        <Layout style={{minHeight: '100vh'}}>
            <Sider 
                style={{
                    minHeight: '100vh'
                }} 
                collapsible collapsed={collapsed} 
                onCollapse={onCollapse} 
                theme="dark"
            >
                <Divider />
                <Menu theme='dark' selectedKeys={[location.pathname]} mode="inline">
                    {menuItems}
                </Menu>
                <Menu theme="dark" selectable={false} mode="inline">
                    <Menu.Divider />
                    <Menu.Item key={'logout'} style={{margin: 0}}>
                        <Logout logout={logout}>
                            <LogoutOutlined />
                            <span>Logout</span>
                        </Logout>
                    </Menu.Item>
                </Menu>
            </Sider>
            <Layout>
                <Header style={headerStyle}>
                    AR-Funitures Management App
                </Header>
                <Content>
                    <Switch>
                        {childrens}
                    </Switch>
                </Content>
                <Footer style={footerStyle}>
                    <div>Power by Ant-Design</div>
                </Footer>
            </Layout>
        </Layout>
    );
};

const headerStyle = {
    position: 'relative',
    padding: '4px',
    margin: 0,
    backgroundColor: '#24b7d3',
    color: '#ffffff',
    fontSize: '32px',
    textAlign: 'center',
    textJustify: 'middle',
    height: '80px'
}

const footerStyle = {
    textAlign: 'center', 
    backgroundColor: '#24b7d3',
    color: '#ffffff'
}

export { MainLayout };