import React, { useState, useEffect } from 'react';
import { PageHeader, Button, Spin, } from 'antd';
import { EditableTable, TableFormModal } from '../components';

const RoomType = ({ 
    isFetching,
    items,  
    getRoomType,
    getRoomTypeById,
    createRoomType,
    editRoomTypeById,
    deleteRoomTypeById
}) => {

    const [visible, setVisible] = useState(false);

    const onOk = (data) => {
        setVisible(false);
        createRoomType(data);
    };
    
    const onCancel = () => {
        setVisible(false);
    }

    const onOpen = () => {
        setVisible(true);
    }

    const dataSource = items.map((item, index) => {
        return Object.assign({}, item, { key: item.id, no: index + 1})
    });
    const columns = [
        {
            title: 'No',
            dataIndex: 'no',
            key: 'no',
            width: '25%',
            render: (no) => (
                <span>{no}</span>
            )
        },
        {
            title: 'Name',
            dataIndex: 'name',
            key: 'name',
            editable: true,
            width: '50%',
            sorter: (a, b) => a.name.localeCompare(b.name)
        },
    ];

    
    useEffect(() => {
        getRoomType();
    // eslint-disable-next-line
    }, []);

    return (
        <Spin spinning={isFetching}>
            <TableFormModal 
                title={'Add a new room types'}
                columns={columns} 
                visible={visible} 
                onOk={onOk}
                onCancel={onCancel}
                okText={'Submit'}
            />
            <PageHeader title={'Room Types'}>
                <Button onClick={onOpen} type="primary">New</Button>
            </PageHeader>
            <EditableTable 
                getAll={getRoomType}
                getById={getRoomTypeById}
                editById={editRoomTypeById}
                deleteById={deleteRoomTypeById}

                dataSource={dataSource} 
                columns={columns} 
                style={{margin: '0 24px'}} 
            />
        </Spin>
    );
};

export { RoomType };