import { roomTypeDataService } from '../services';
// action types and action creator for roomType
const FETCHED_ALL_ROOM_TYPE = 'FETCHED_ALL_ROOM_TYPE'
const ROOM_TYPE_DETAIL = 'ROOM_TYPE_DETAIL'

const ROOM_TYPE_CREATED = 'ROOM_TYPE_CREATED'
const ROOM_TYPE_EDITED = 'ROOM_TYPE_EDITED'
const ROOM_TYPE_DELETED = 'ROOM_TYPE_DELETED'
const ROOM_TYPE_IS_FETCHING = 'ROOM_TYPE_IS_FETCHING'
const ROOM_TYPE_HAS_ERROR = 'ROOM_TYPE_HAS_ERROR'

export const roomTypeActionType = {
    FETCHED_ALL_ROOM_TYPE,
    ROOM_TYPE_DETAIL,
    ROOM_TYPE_CREATED,
    ROOM_TYPE_EDITED,
    ROOM_TYPE_DELETED,
    ROOM_TYPE_IS_FETCHING,
    ROOM_TYPE_HAS_ERROR
}
export const roomTypeAction = {
    getRoomType,
    getRoomTypeById,
    createRoomType,
    editRoomTypeById,
    deleteRoomTypeById
}

function createRoomType(payload) {
    return dispatch => {
        dispatch(roomTypeIsFetching);
        roomTypeDataService.create(payload)
        .then((response) => {
            dispatch(roomTypeCreated(response.data));
        })
        .catch((error) => {
            dispatch(roomTypeHasError());
            console.error(error);
        })
    }
}
function editRoomTypeById(id, payload) {
    return dispatch => {
        dispatch(roomTypeIsFetching);
        roomTypeDataService.update(id, payload)
        .then((response) => {
            dispatch(roomTypeEdited(response.data));
        })
        .catch((error) => {
            dispatch(roomTypeHasError());
            console.error(error);
        })
    }
}
function deleteRoomTypeById(id) {
    return dispatch => {
        dispatch(roomTypeIsFetching);
        roomTypeDataService.delete(id)
        .then((response) => {
            dispatch(roomTypeDeleted({id: id}));
        })
        .catch((error) => {
            dispatch(roomTypeHasError(error));
            console.error(error);
        })
    }
};
function getRoomType() {
    return async dispatch => {
        dispatch(roomTypeIsFetching);
        try {
            let {data} = await roomTypeDataService.getAll();
            dispatch(roomTypeListUpdate(data));
        } catch(error) {
            dispatch(roomTypeHasError());
            console.error(error);
        }
    }
};
function getRoomTypeById(id) {
    return dispatch => {
        dispatch(roomTypeIsFetching);
        roomTypeDataService.get(id)
        .then((response) => {
            dispatch(roomTypeDetail(response.data));
        })
        .catch((error) => {
            dispatch(roomTypeHasError());
            console.error(error);
        })
    }
};


const roomTypeCreated = (data) => ({
    type: ROOM_TYPE_CREATED,
    data
});
const roomTypeEdited = (data) => ({
    type: ROOM_TYPE_EDITED,
    data
});
const roomTypeDeleted = (data) => ({
    type: ROOM_TYPE_DELETED,
    data
});
const roomTypeIsFetching = ({
    type: ROOM_TYPE_IS_FETCHING
});
const roomTypeListUpdate = (items) => ({
    type: FETCHED_ALL_ROOM_TYPE,
    items
});
const roomTypeDetail = (item) => ({
    type: ROOM_TYPE_DETAIL,
    item
});

const roomTypeHasError = (error) => ({
    type: ROOM_TYPE_HAS_ERROR,
    error
});