import React from 'react';
import { Router, Switch, Route } from 'react-router-dom';
import { PrivateRoute } from './components';
import { Error404 } from './components/error-404.component';
import { MainLayout } from './containers/main-layout.container';
import { history } from './helpers';
import { Login } from './containers';
import { useDispatch } from 'react-redux';
import {
  roomTypeAction,
  furnitureTypeAction,
  furnitureColorAction,
  furnitureMaterialAction,
  furnitureAction,
  versionAction
} from './actions/';
import 'antd/dist/antd.css';
import './css/App.css';

const loadAllData = async (dispatch) => {
  dispatch(versionAction.fetchAllVersion());
  await dispatch(roomTypeAction.getRoomType());
  await dispatch(furnitureTypeAction.fetchAllFurnitureType());
  await dispatch(furnitureColorAction.fetchAllFurnitureColor());
  await dispatch(furnitureMaterialAction.fetchAllFurnitureMaterial());
  await dispatch(furnitureAction.fetchAllFurniture());
}

function App() {

  const dispatch = useDispatch();
  loadAllData(dispatch);

  return (
    <div>
      <Router history={history}>
        <Switch>
          <Route exact path='/login/' component={Login} />
          <PrivateRoute exact path='/' component={MainLayout} />
          <PrivateRoute exact path='/room-types/' component={MainLayout} />
          <PrivateRoute exact path='/furniture-types/' component={MainLayout} />
          <PrivateRoute exact path='/furniture-colors/' component={MainLayout} />
          <PrivateRoute exact path='/furniture-materials/' component={MainLayout} />
          <PrivateRoute exact path='/furnitures/' component={MainLayout} />
          <PrivateRoute exact path='/addressables/' component={MainLayout} />
          <PrivateRoute exact path='/versions/' component={MainLayout} />
          <Route path='*' component={Error404} />
        </Switch>
      </Router>
    </div>
  );
}

export default App;
