import { connect } from 'react-redux';
import { Version } from '../components/version.component';
import { versionAction as Action } from '../actions/version.action';

const mapStateToProps = state => {
    return Object.assign({}, state.versions);
};

const mapDispatchToProps = dispatch => {
    return {
        fetchAllVersion: () => dispatch(Action.fetchAllVersion()),
        createVersion: (payload) => dispatch(Action.createVersion(payload)),
    }
};

const connectedVersion = connect(mapStateToProps, mapDispatchToProps)(Version);
export { connectedVersion as Version };