import React, { useState, useEffect } from 'react';
import {
    Form, 
    InputNumber, 
    Input, 
    Select, 
    Modal, 
    List, 
} from 'antd';

const { Option } = Select;
const { TextArea } = Input;

const ReferenceForm = ({
    columns,
    onSubmit,
    onCancel,
    action, 
    /*
        action = { 
            type: 'create' || 'update || 'display',
            data: formValues
        }

    */
    title,
    ...restProps
}) => {

    const [form] = Form.useForm();
    const [formRef, setFormRef] = useState();
    const [formTitle, setFormTitle] = useState();
    
    useEffect(() => {
        if(formRef) {
            //let data = action.type === 'update' || action.type === 'display' ? action.data : {};
            form.setFieldsValue(action.data);

            return () => {
                form.resetFields();
            }
        }
    });

    useEffect(() => {
        let titleForm = () => {
            switch(action.type) {
                case('create'):
                    return `Add a new ${title.toLowerCase()}`;
                case('update'):
                    return `Edit a ${title.toLowerCase()}`;
                case('display'):
                    return `Detail a ${title.toLowerCase()}`;
                default:
                    return `Undefined title`;
            }
        };

        setFormTitle(titleForm);
    // eslint-disable-next-line
    }, [action.type]);
    
    const fields = columns.filter(col => col.editable);
    const listItems = fields.map((field, index) => {
        let inputNode;
        if(field.selectable) {
            inputNode = 
                <Select
                    placeholder={`Choose ${title.toLowerCase()} ${field.title.toLowerCase()}`}
                    mode={field.mode}
                >
                    {field.options.map((option, index) => (
                        <Option 
                            key={index}
                            value={option.value}
                        >
                            {option.text}
                        </Option>
                    ))}
                </Select>
        } else {
            inputNode = field.inputType === 'number' ? 
                <InputNumber 
                    style={{width: '100%'}} 
                    placeholder={`Enter ${title.toLowerCase()} ${field.title.toLowerCase()}`}
                /> : 
                field.inputType === 'textArea' ? 
                <TextArea 
                    autoSize={{ minRows: 4, maxRows: 10 }} 
                    allowClear
                    placeholder={`Enter ${title.toLowerCase()} ${field.title.toLowerCase()}`}
                /> :
                <Input placeholder={`Enter ${title.toLowerCase()} ${field.title.toLowerCase()}`} />
        }

        return (
            <Form.Item
                key={index}
                label={field.title}
                name={field.dataIndex}
                rules={[
                    {
                        required: true,
                        message: `Please input ${field.title.toLowerCase()}`,
                    },
                ]}
            >
                {inputNode}
            </Form.Item>
        );
    });

    const onOk = async () => {
        try {
            let data = await form.validateFields();
            form.resetFields();
            onSubmit({action: action, result: data});
        } catch(error) {
            //console.log(error);
        }
    };

    return(
        action.type === 'display' ?
        (
            <Modal 
                title={formTitle}
                onOk={onCancel}
                okText={'OK'}
                onCancel={onCancel}
                cancelButtonProps={{style: {display: 'none'}}}
                {...restProps}
            >
                <List 
                    bordered
                    dataSource={columns}
                    renderItem={item => (
                        <List.Item>
                            <List.Item.Meta 
                                title={<b>{item.title}</b>}
                                description={
                                    item.render ? item.render(action.data[item.dataIndex]) : 
                                    item.selectable ? 
                                    item.options.find(option => option.value === action.data[item.dataIndex]).text : 
                                    action.data[item.dataIndex]
                                }
                            />
                        </List.Item>
                    )}
                />
            </Modal>
        ) : 
        (
            <Modal
                title={formTitle}
                onOk={onOk}
                okText={'Submit'}
                onCancel={onCancel}
                {...restProps}
            >
                <Form
                    form={form}
                    ref={setFormRef}
                    layout="vertical"
                >
                    {listItems}
                </Form>
            </Modal>
        )
    );
};

export { ReferenceForm };