import { connect } from 'react-redux';
import { Furniture } from '../components/furniture.component';
import { furnitureAction as Action } from '../actions';

const mapStateToProps = state => {
    return Object.assign({}, state.furnitures);
};

const mapDispatchToProps = dispatch => {
    return {
        fetchAllFurniture: () => dispatch(Action.fetchAllFurniture()),
        createFurniture: (payload) => dispatch(Action.createFurniture(payload)),
        editFurniture: (id, payload) => dispatch(Action.editFurniture(id, payload)),
        deleteFurniture: (id) => dispatch(Action.deleteFurniture(id)),
    }
};

const connectedFurniture = connect(mapStateToProps, mapDispatchToProps)(Furniture);
export { connectedFurniture as Furniture };