import { versionActionType as ActionType }  from '../actions/version.action';

const initState = ({
    isFetching: false,
    hasError: false,
    items: []
});

const add = (list, newItem) => {
    let newList = [...list];
    newList.push(newItem);
    return newList;
};

export const version = (state = initState, action) => {
    switch(action.type) {
        case(ActionType.VERSION_IS_FETCHING):
            return Object.assign({}, state, { isFetching: true, hasError: false });
        case(ActionType.VERSION_FETCHED_ALL):
            return Object.assign({}, state, { isFetching: false, items: action.data });
        case(ActionType.VERSION_HAS_ERROR):
            return Object.assign({}, state, { isFetching: false, hasError: true });
        case(ActionType.VERSION_CREATED):
            return Object.assign({}, state, { isFetching: false },
                { items: add(state.items, action.data) }
            );
        default:
            return Object.assign({}, state);
    }
}