import React, { useState } from 'react';
import { Form, Spin, PageHeader, Select, Button, Upload, message, Space } from 'antd';
import http from '../services/http-common';
import { UploadOutlined } from '@ant-design/icons';
import { token as Token } from '../helpers';

const { Option } = Select;
const layout = {
    labelCol: {
      span: 8,
    },
    wrapperCol: {
      span: 8,
    },
};
const tailLayout = {
    wrapperCol: {
      offset: 8,
      span: 16,
    },
  };
const Addressable = () => {
    const [isFetching, setIsFetching] = useState(false);
    const [form] = Form.useForm();

    const [fileHash, setFileHash] = useState([]);
    const [fileJson, setFileJson] = useState([]);
    const [fileBundle, setFileBundle] = useState([]);

    const onFinish = (fields) => {
        setIsFetching(true);
        let formData = new FormData()
        
        formData.append('file_hash', fields.file_hash.file);
        formData.append('file_json', fields.file_json.file);
        formData.append('file_bundle', fields.file_bundle.file);
        formData.append('target_build', fields.target);
        
        http.post('/upload-addressables/', formData, {
            headers: {
                'Content-Type': 'multipart/form-data',
                'Authorization': 'Bearer ' + Token.getToken().access,
            }
        })
        .then(response => {
            setIsFetching(false);
            form.resetFields();
            message.success('Uploaded sucessfully!', 5);
            onReset();
        })
        .catch(error => {
            setIsFetching(false);
            message.error('Unexpected error has occured!')
            console.log(error);
        })
    };

    const onReset = () => {
        setFileHash(null);
        setFileJson(null);
        setFileBundle(null);
        form.resetFields();
    };
    
    return(
        <Spin spinning={isFetching}>
            <PageHeader title={'Unity Addressables'}></PageHeader>
            <h1 style={{textAlign: 'center'}}>Upload addressables Unity</h1>
            <Form
                form={form}
                initialValues={{target: 'Android'}}
                onFinish={onFinish}
                style={{
                    margin: '24px'
                }}
                {...layout}
            >
                <Form.Item
                    label="Target"
                    name="target"
                    rules={[
                        {
                            required: true,
                            message: 'Please choose the target.'
                        }
                    ]}
                >
                    <Select>
                        <Option value="Android">Android</Option>
                    </Select>
                </Form.Item>
                <Form.Item
                    label="File hash"
                    name="file_hash"
                    rules={[
                        {
                            required: true,
                            message: 'Please select file hash.'
                        }
                    ]}
                >
                    <Upload 
                        accept=".hash"
                        beforeUpload={file => {
                            setFileHash([file]);
                            return false;
                        }}
                        fileList={fileHash}
                    >
                        <Button><UploadOutlined />File hash</Button>
                    </Upload>
                </Form.Item>
                <Form.Item
                    label="File json"
                    name="file_json"
                    rules={[
                        {
                            required: true,
                            message: 'Please select file json.'
                        }
                    ]}
                >
                    <Upload 
                        accept=".json"
                        beforeUpload={file => {
                            setFileJson([file]);
                            return false;
                        }}
                        fileList={fileJson}
                    >
                        <Button><UploadOutlined />File json</Button>
                    </Upload>
                </Form.Item>
                <Form.Item
                    label="File bundle"
                    name="file_bundle"
                    rules={[
                        {
                            required: true,
                            message: 'Please select file bundle.'
                        }
                    ]}
                >
                    <Upload 
                        accept=".bundle"
                        beforeUpload={file => {
                            setFileBundle([file]);
                            return false;
                        }}
                        fileList={fileBundle}
                    >
                        <Button><UploadOutlined />File bundle</Button>
                    </Upload>
                </Form.Item>
                <Form.Item {...tailLayout}>
                    <Space>
                        <Button type="primary" htmlType="submit">
                        Submit
                        </Button>
                        <Button 
                            type="default" 
                            htmlType="reset"
                            onClick={onReset}
                        >
                        Reset
                        </Button>
                    </Space>
                </Form.Item>
            </Form>
        </Spin>
    );
};

export { Addressable }