import React, { useState, useEffect } from 'react';
import { 
    Spin, 
    PageHeader, 
    Button,
    Table, 
} from 'antd';
import {
    ReferenceForm, 
} from '../components';

const Version = ({
    items,
    isFetching,
    fetchAllVersion,
    createVersion,
}) => {

    const dataSource = items.map((item, index) => {
        return Object.assign({}, item, { key: item.id, no: index + 1});
    });

    const [visibleForm, setVisibleForm] = useState(false);
    const [actionForm, setActionForm] = useState({action: '', data: {}});
    const onSubmitForm = ({action, result}) => {
        setVisibleForm(false);
        if(action.type === 'create') {
            createVersion(result);
        }
    };

    const columns = [
        {
            title: 'No',
            dataIndex: 'no',
            width: '15%'
        },
        {
            title: 'Number',
            dataIndex: 'name',
            editable: true,
            inputType: 'text',
            width: '20%',
        },
        {
            title: 'Date',
            dataIndex: 'date',
            width: '30%',
            sorter: (a, b) => new Date(a.date) - new Date(b.date),
            render: date => (
                <span>{new Date(date).toLocaleString()}</span>
            )
        },
        {
            title: 'Message',
            dataIndex: 'message',
            editable: true,
            inputType: 'textArea',
            width: '35%',
        },
    ];

    useEffect(() => {
        fetchAllVersion();
    // eslint-disable-next-line
    }, []);

    return (
        <Spin spinning={isFetching}>
            
            <ReferenceForm 
                title={'Version'}
                visible={visibleForm}
                columns={columns}
                onSubmit={onSubmitForm}
                onCancel={() => setVisibleForm(false)}
                action={actionForm}
            />
            <PageHeader title={'Versions'}>
                <Button type="primary" onClick={() => {
                    setActionForm({type: 'create', data: {}})
                    setVisibleForm(true);
                }}
                >Publish a new version</Button>
            </PageHeader>
            <Table 
                dataSource={dataSource} 
                columns={columns} 
                style={{
                    margin: '0 24px'
                }}
            />
        </Spin>
    );
};

export { Version };