import { authenticationActionType as ActionType } from '../actions';

const initState = ({
    loggingIn: false,
});
const authentication = (state = initState, action) => {
    switch(action.type) {
        case(ActionType.LOGGING_IN):
            return Object.assign({}, state, { loggingIn: true, logInError: false });
        case(ActionType.LOGGED_IN):
            return Object.assign({}, state, { loggingIn: false });
        case(ActionType.LOG_IN_ERROR):
            return Object.assign({}, state, { loggingIn: false, logInError: true });
        case(ActionType.FETCHED_USER_DATA):
            return Object.assign({}, state, { auth: action.auth });
        case(ActionType.LOGGED_OUT):
            return Object.assign({}, state, { auth: false });
        default:
            return state;
    }
};

export { authentication };