import React, { useState, useEffect } from 'react';
import { useSelector } from 'react-redux';
import { 
    Spin, 
    PageHeader, 
    Button,
} from 'antd';
import { 
    ReferenceTable, 
    ReferenceForm, 
} from '../components';

const FurnitureType = ({
    items,
    isFetching,
    fetchAllFurnitureType,
    createFurnitureType,
    editFurnitureType,
    deleteFurnitureType,
}) => {

    const roomTypes = useSelector(state => state.roomTypes.items);

    const roomTypeOptions = roomTypes.map(room => {
        return Object.assign({}, {value: room.id, text: room.name});
    });

    const dataSource = items.map((item, index) => {
        return Object.assign({}, item, { key: item.id, no: index + 1});
    });

    const [visibleForm, setVisibleForm] = useState(false);
    const [actionForm, setActionForm] = useState({action: '', data: {}});
    const onSubmitForm = ({action, result}) => {
        setVisibleForm(false);
        if(action.type === 'create') {
            createFurnitureType(result)
        } else if(action.type === 'update') {
            editFurnitureType(action.data.id, result);
        }
    };

    const columns = [
        {
            title: 'No',
            dataIndex: 'no',
            width: '15%'
        },
        {
            title: 'Name',
            dataIndex: 'name',
            editable: true,
            inputType: 'text',
            width: '30%',
            sorter: (a, b) => a.name.localeCompare(b.name)
        },
        {
            title: 'Room Type',
            dataIndex: 'room',
            editable: true,
            selectable: true,
            options: roomTypeOptions,
            width: '30%',
            filters: roomTypeOptions,
            onFilter: (value, record) => record.room === value,
        },
    ];

    useEffect(() => {
        fetchAllFurnitureType();
    // eslint-disable-next-line
    }, []);

    return (
        <Spin spinning={isFetching}>
            
            <ReferenceForm 
                title={'Furniture Type'}
                visible={visibleForm}
                columns={columns}
                onSubmit={onSubmitForm}
                onCancel={() => setVisibleForm(false)}
                action={actionForm}
            />
            <PageHeader title={'Furniture Types'}>
                <Button type="primary" onClick={() => {
                    setActionForm({type: 'create', data: {}})
                    setVisibleForm(true);
                }}
                >New</Button>
            </PageHeader>
            <ReferenceTable 
                dataSource={dataSource} 
                columns={columns} 
                style={{
                    margin: '0 24px'
                }}
                onHandleEdit={(record) => {
                    setActionForm({type: 'update', data: record})
                    setVisibleForm(true);
                }}
                onHandleDetail={(record) => {
                    setActionForm({type: 'display', data: record})
                    setVisibleForm(true);
                }}
                onHandleDelete={(record) => {
                    deleteFurnitureType(record.id);
                }}
            />
        </Spin>
    );
};

export { FurnitureType };