import { connect } from 'react-redux';
import { FurnitureType } from '../components';
import { furnitureTypeAction as Action } from '../actions';

const mapStateToProps = state => {
    return Object.assign({}, state.furnitureTypes);
};

const mapDispatchToProps = dispatch => {
    return {
        fetchAllFurnitureType: () => dispatch(Action.fetchAllFurnitureType()),
        createFurnitureType: (payload) => dispatch(Action.createFurnitureType(payload)),
        editFurnitureType: (id, payload) => dispatch(Action.editFurnitureType(id, payload)),
        deleteFurnitureType: (id) => dispatch(Action.deleteFurnitureType(id)),
    }
};

const connectedFurnitureType = connect(mapStateToProps, mapDispatchToProps)(FurnitureType);
export { connectedFurnitureType as FurnitureType };