import {
    //roomTypeAction as Action, 
    roomTypeActionType as ActionType
} from '../actions/room-type.action';

const initState = {
    isFetching: true,
    items: []
};
const roomType = (state = initState, action) => {
    let newItems = [];
    let index = 0;
    switch(action.type) {
        case ActionType.ROOM_TYPE_IS_FETCHING:
            return Object.assign({}, state, {isFetching: true, hasError: false});
        case ActionType.ROOM_TYPE_HAS_ERROR:
            return Object.assign({}, state, {isFetching: false, hasError: true});
        case ActionType.FETCHED_ALL_ROOM_TYPE:
            return Object.assign({}, state, {items: action.items}, {isFetching: false});
        case ActionType.ROOM_TYPE_DETAIL:
            return Object.assign({}, state, {itemDetail: action.item}, {isFetching: false});
        case ActionType.ROOM_TYPE_CREATED:
            newItems = [...state.items];
            newItems.push(action.data);
            return Object.assign({}, state, {isFetching: false}, 
                { items:  newItems}
            );
        case ActionType.ROOM_TYPE_EDITED:
            newItems = [...state.items];
            index = newItems.findIndex(item => item.id === action.data.id);
            newItems.splice(index, 1, action.data);
            return Object.assign({}, state, {isFetching: false},
                { items: newItems }
            );
        case ActionType.ROOM_TYPE_DELETED:
            return Object.assign({}, state, {isFetching: false},
                Object.assign({}, {items: state.items.filter(item => item.id !== action.data.id)})
            );
        default:
            return state;
    }
}

export { roomType };