const cookie = {
    setCookie: function(cName, cValue, expireTime = false) {
        console.log(expireTime);
        if(expireTime) {
            let expires = "expires="+ expireTime.toUTCString();
            document.cookie = cName + "=" + escape(cValue) + ";" + expires + ";path=/";

            document.cookie = cName + "_expireTime="+ expireTime + ";" + expires + ";path=/";
        }
        else {
            document.cookie = cName + "=" + escape(cValue) + ";path=/";

            document.cookie = cName + "_expireTime=;expires=;path=/";
        }
    },
 
    getCookie: function(cName) {
        let name = cName + "=";
        let decodedCookie = decodeURIComponent(document.cookie);
        let ca = decodedCookie.split(';');
        for(let i = 0; i < ca.length; i++) {
            let c = ca[i];
            while (c.charAt(0) === ' ') {
                c = c.substring(1);
            }
            if (c.indexOf(name) === 0) {
                return unescape(c.substring(name.length, c.length));
            }
        }
        return null;
    },

    removeCookie: function(cName) {
        let d = new Date();
        let expires = "expires="+ d.toUTCString();
        document.cookie = cName + "=;" + expires + ";path=/";
    },

    getExpireCookie: function(cName) {
        let name = cName + "_expireTime=";
        let decodedCookie = decodeURIComponent(document.cookie);
        console.log(decodedCookie);
        let ca = decodedCookie.split(';');
        for(let i = 0; i < ca.length; i++) {
            let c = ca[i];
            while (c.charAt(0) === ' ') {
                c = c.substring(1);
            }
            if (c.indexOf(name) === 0) {
                return new Date(unescape(c.substring(name.length, c.length)));
            }
        }
        return false;
    },
};

export { cookie };