import http from './http-common';
import { token as Token } from '../helpers';
import { authenticationDataService } from './authentication.service';

http.interceptors.response.use(
    response => response, 
    error => {
        const originalRequest = error.config;
        if(error.response.status === 401 && error.response.statusText === 'Unauthorized'){
            if(Token.tokenRefreshIsValid()) {
                let token = Token.getToken();
                return authenticationDataService.refresh({refresh: token.refresh})
                    .then(response => {
                        Token.updateToken(response.data);
                        http.defaults.headers = { 
                            Authorization: 'Bearer ' + response.data.access,
                            "Content-type": "application/json" 
                        };
                        originalRequest.headers = { 
                            Authorization: 'Bearer ' + response.data.access,
                            "Content-type": "application/json" 
                        };
                        return http(originalRequest);
                    })
                    .catch(error => {
                        console.error(error);
                    });
            }
            else {
                Token.removeToken();
                Promise.reject(error);
            }
        }
        return Promise.reject(error);
    }
);

const requestCRUDService = (queryPath) => {
    return {
        getAll: () => { 
            return http.get(`${queryPath}/`); 
        },
        get: (id) => { 
            return http.get(`${queryPath}/${id}/`); 
        },
        create: (data) => { 
            return http.post(`${queryPath}/`, data, getOptions()); 
        },
        update: (id, data) => { 
            return http.put(`${queryPath}/${id}/`, data, getOptions()); 
        },
        partialUpdate: (id, data) => {
            return http.patch(`${queryPath}/${id}/`, data, getOptions()); 
        },
        delete: (id) => { 
            return http.delete(`${queryPath}/${id}/`, getOptions()); 
        },
        searchBy: (params) => {
            let str_filter = '?';
            params.forEach(element => {
                str_filter += `${element.field}=${element.value}`;
            });
            return http.get(`${queryPath}/${str_filter}`);
        }
    }
}

function getOptions() {
    let options = {};
    if(Token.tokenRefreshIsValid()){
        options.headers = { 
            Authorization: 'Bearer ' + Token.getToken().access,
            "Content-type": "application/json" 
        };
    }
    return options;
}

export default requestCRUDService;