import React, { useState } from 'react';
import { Table, InputNumber, Input, Form, Space, Popconfirm, Button } from 'antd';
import { 
    QuestionCircleOutlined, 
    EditOutlined,
    DeleteOutlined,
    QuestionOutlined,
} from '@ant-design/icons';

const EditableCell = ({
    editing,
    dataIndex,
    title,
    inputType,
    record,
    index,
    children,
    ...restProps
}) => {
    const inputNode = inputType === 'number' ? <InputNumber /> : <Input />;
    return (
        <td {...restProps}>
            {editing ? (
                <Form.Item
                    name={dataIndex}
                    style={{
                        margin: 0
                    }}
                    rules={[
                        {
                            required: true,
                            message: `Please Input ${title}!`,
                        },
                    ]}
                >
                    {inputNode}
                </Form.Item>
            ) : (children)}
        </td>
    );
};

const EditableTable = ({
    dataSource, 
    columns, 
    getAll,
    getById,
    editById,
    deleteById,
    ...restProps
}) => {
    const [form] = Form.useForm();
    const [editingKey, setEditingKey] = useState('');

    const isEditing = record => record.key === editingKey;

    const handleEdit = record => {
        form.setFieldsValue({...record});
        setEditingKey(record.key);
    };

    const handleCancel = () => {
        setEditingKey('');
    };

    const handleSave = async key => {
        try {
            const row = await form.validateFields();
            const index = dataSource.findIndex(item => item.key === key);
            if(index > -1) {
                let payload = JSON.stringify(row);
                let id = dataSource[index].id;
                editById(id, payload);
                setEditingKey('');
            } else {
                //let payload = JSON.stringify(row);
                //console.log(`Add ${payload}`);
                setEditingKey('');
            }
        } catch(error) {
            console.log(`Validate Failed: ${error}`);
        }   
    };

    const handleDelete = record => {
        deleteById(record.id);
    }

    const expandColumns = columns => {
        const newColumns = [...columns];
        const operationColumn = {
            title: 'Operation',
            dataIndex: 'operation',
            render: (_, record) => {
                const editable = isEditing(record);
                return editable ? (
                    <span>
                        <Space>
                            <Button 
                                onClick={() => handleSave(record.key)}
                                type="primary"
                            >Save</Button>
                            <Popconfirm 
                                title={<span>Your editing will be not saved.<br/>Continue anyway?</span>}
                                cancelText={'Keep editing'}
                                okText={'Discard'} 
                                okType="danger"
                                onConfirm={handleCancel}
                                icon={<QuestionOutlined style={{color: '#ff0000'}} />}
                            >
                                <Button>Cancel</Button>
                            </Popconfirm>
                        </Space>
                    </span>
                ) : (
                    <span>
                        <Space>
                            <Button
                                disabled={editingKey !== ''}
                                onClick={() => handleEdit(record)}
                                title={'Edit'}
                            ><EditOutlined /></Button>
                            <Popconfirm 
                                disabled={editingKey !== ''}
                                title={<span>Critical! Are you sure to delete?</span>} 
                                okText={'Confirm'}
                                okType="danger"
                                onConfirm={() => handleDelete(record)}
                                icon={<QuestionCircleOutlined style={{color: '#ff0000'}} />}
                            >
                                <Button
                                    disabled={editingKey !== ''}
                                    title={'Delete'}
                                ><DeleteOutlined style={{color: '#ff0000'}} /></Button>
                            </Popconfirm>
                        </Space>
                    </span>
                );
            }
        }
        newColumns.push(operationColumn)
        return newColumns;
    }

    const mergedColumns = expandColumns(columns).map(col => {
        if(!col.editable) {
            return col;
        }

        return {
            ...col,
            onCell: record => ({
                record,
                inputType: col.type === 'number' ? 'number' : 'text',
                dataIndex: col.dataIndex,
                title: col.title,
                editing: isEditing(record),
            })
        }
    });
    return (
        <Form form={form} component={false}>
            <Table 
                components={{
                    body: {
                        cell: EditableCell,
                    },
                }}
                bordered
                dataSource={dataSource}
                columns={mergedColumns}
                pagination={{
                    onChange: handleCancel,
                }}
                {...restProps}
            />
        </Form>
    )
};

export { EditableTable };