import { furnitureColorActionType as ActionType }  from '../actions';

const initState = ({
    isFetching: false,
    hasError: false,
    items: []
});

const add = (list, newItem) => {
    let newList = [...list];
    newList.push(newItem);
    return newList;
};

const remove = (list, itemRemove) => {
    let newList = [...list];
    return newList.filter(item => item.id !== itemRemove.id);
};

const replace = (list, itemReplace) => {
    let newList = [...list];
    let index = newList.findIndex(item => item.id === itemReplace.id);
    newList.splice(index, 1, itemReplace);
    return newList;
};

export const furnitureColor = (state = initState, action) => {
    switch(action.type) {
        case(ActionType.FURNITURE_COLOR_IS_FETCHING):
            return Object.assign({}, state, { isFetching: true, hasError: false });
        case(ActionType.FURNITURE_COLOR_FETCHED_ALL):
            return Object.assign({}, state, { isFetching: false, items: action.data });
        case(ActionType.FURNITURE_COLOR_HAS_ERROR):
            return Object.assign({}, state, { isFetching: false, hasError: true });
        case(ActionType.FURNITURE_COLOR_CREATED):
            return Object.assign({}, state, { isFetching: false },
                { items: add(state.items, action.data) }
            );
        case(ActionType.FURNITURE_COLOR_EDITED):
            return Object.assign({}, state, { isFetching: false },
                { items: replace(state.items, action.data) }
            );
        case(ActionType.FURNITURE_COLOR_DELETED):
            return Object.assign({}, state, { isFetching: false },
                { items: remove(state.items, action.data) }
            );
        default:
            return Object.assign({}, state);
    }
}