import React, { useState, useEffect } from 'react';
import { 
    Spin, 
    PageHeader, 
    Button,
} from 'antd';
import { 
    ReferenceTable, 
    ReferenceForm, 
} from '../components';

const fengshuiElements = [
    {
        value: 1,
        text: 'Kim'
    },
    {
        value: 2,
        text: 'Mc'
    },
    {
        value: 3,
        text: 'Thy'
    },
    {
        value: 4,
        text: 'Ha'
    },
    {
        value: 5,
        text: 'Th'
    }
];

const FurnitureMaterial = ({
    items,
    isFetching,
    fetchAllFurnitureMaterial,
    createFurnitureMaterial,
    editFurnitureMaterial,
    deleteFurnitureMaterial,
}) => {

    const dataSource = items.map((item, index) => {
        return Object.assign({}, item, { key: item.id, no: index + 1});
    });

    const [visibleForm, setVisibleForm] = useState(false);
    const [actionForm, setActionForm] = useState({action: '', data: {}});
    const onSubmitForm = ({action, result}) => {
        setVisibleForm(false);
        if(action.type === 'create') {
            createFurnitureMaterial(result)
        } else if(action.type === 'update') {
            editFurnitureMaterial(action.data.id, result);
        }
    };

    const columns = [
        {
            title: 'No',
            dataIndex: 'no',
            width: '15%'
        },
        {
            title: 'Name',
            dataIndex: 'name',
            editable: true,
            inputType: 'text',
            width: '30%',
            sorter: (a, b) => a.name.localeCompare(b.name)
        },
        {
            title: 'Fengshui Element',
            dataIndex: 'element',
            editable: true,
            selectable: true,
            options: fengshuiElements,
            width: '30%',
            filters: fengshuiElements,
            onFilter: (value, record) => record.element === value,
        },
    ];

    useEffect(() => {
        fetchAllFurnitureMaterial();
    // eslint-disable-next-line
    }, []);

    return (
        <Spin spinning={isFetching}>
            
            <ReferenceForm 
                title={'Furniture Material'}
                visible={visibleForm}
                columns={columns}
                onSubmit={onSubmitForm}
                onCancel={() => setVisibleForm(false)}
                action={actionForm}
            />
            <PageHeader title={'Furniture Materials'}>
                <Button type="primary" onClick={() => {
                    setActionForm({type: 'create', data: {}})
                    setVisibleForm(true);
                }}
                >New</Button>
            </PageHeader>
            <ReferenceTable 
                dataSource={dataSource} 
                columns={columns} 
                style={{
                    margin: '0 24px'
                }}
                onHandleEdit={(record) => {
                    setActionForm({type: 'update', data: record})
                    setVisibleForm(true);
                }}
                onHandleDetail={(record) => {
                    setActionForm({type: 'display', data: record})
                    setVisibleForm(true);
                }}
                onHandleDelete={(record) => {
                    deleteFurnitureMaterial(record.id);
                }}
            />
        </Spin>
    );
};

export { FurnitureMaterial };