import { furnitureColorDataService as DataService } from '../services';

const FURNITURE_COLOR_FETCHED_ALL = 'FURNITURE_COLOR_FETCHED_ALL';
const FURNITURE_COLOR_IS_FETCHING = 'FURNITURE_COLOR_IS_FETCHING';
const FURNITURE_COLOR_HAS_ERROR = 'FURNITURE_COLOR_HAS_ERROR';
const FURNITURE_COLOR_CREATED = 'FURNITURE_COLOR_CREATED';
const FURNITURE_COLOR_EDITED = 'FURNITURE_COLOR_EDITED';
const FURNITURE_COLOR_DELETED = 'FURNITURE_COLOR_DELETED';

export const furnitureColorActionType = {
    FURNITURE_COLOR_FETCHED_ALL,
    FURNITURE_COLOR_IS_FETCHING,
    FURNITURE_COLOR_HAS_ERROR,
    FURNITURE_COLOR_CREATED,
    FURNITURE_COLOR_EDITED,
    FURNITURE_COLOR_DELETED,
};

export const furnitureColorAction = {
    fetchAllFurnitureColor,
    createFurnitureColor,
    editFurnitureColor,
    deleteFurnitureColor,
};

const furnitureColorFetchedAll = (data) => ({
    type: FURNITURE_COLOR_FETCHED_ALL,
    data
});

const furnitureColorIsFetching = () => ({
    type: FURNITURE_COLOR_IS_FETCHING
});

const furnitureColorCreated = (data) => ({
    type: FURNITURE_COLOR_CREATED,
    data
});

const furnitureColorEdited = (data) => ({
    type: FURNITURE_COLOR_EDITED,
    data
});

const furnitureColorDeleted = (data) => ({
    type: FURNITURE_COLOR_DELETED,
    data
});

const furnitureColorHasError = (error) => ({
    type: FURNITURE_COLOR_HAS_ERROR,
    error
});

function fetchAllFurnitureColor() {
    return async dispatch => {
        dispatch(furnitureColorIsFetching());
        try {
            let { data } = await DataService.getAll();
            dispatch(furnitureColorFetchedAll(data));
        } catch(error) {
            dispatch(furnitureColorHasError());
            console.log(error);
        }
    }
}

function createFurnitureColor(payload) {
    return dispatch => {
        dispatch(furnitureColorIsFetching());
        DataService.create(payload)
        .then(response => {
            dispatch(furnitureColorCreated(response.data));
        })
        .catch(error => {
            dispatch(furnitureColorHasError());
            console.log(error);
        });
    }
}

function editFurnitureColor(id, payload) {
    return dispatch => {
        dispatch(furnitureColorIsFetching());
        DataService.update(id, payload)
        .then(response => {
            dispatch(furnitureColorEdited(response.data));
        })
        .catch(error => {
            dispatch(furnitureColorHasError());
            console.log(error);
        });
    }
}

function deleteFurnitureColor(id) {
    return dispatch => {
        dispatch(furnitureColorIsFetching());
        DataService.delete(id)
        .then(response => {
            dispatch(furnitureColorDeleted({id: id}));
        })
        .catch(error => {
            dispatch(furnitureColorHasError(error));
            //console.log(error);
        });
    }
}