import React, { useEffect } from 'react';
import { Form, Input, Button, Checkbox, Typography, message, Spin, Row, Col  } from 'antd';
import { UserOutlined, LockOutlined } from '@ant-design/icons';
import { Redirect } from 'react-router-dom';
import { token as Token } from '../helpers';

const { Title } = Typography;
const layout = {
  labelCol: {
    span: 8,
  },
  wrapperCol: {
    span: 8,
    //
    offset: 8,
  },
};
const tailLayout = {
  wrapperCol: {
    offset: 8,
    span: 8,
  },
};

const LoginForm = ({ login, logInError }) => {
  useEffect(() => {
    if(logInError){
      message.error('Username or password isn\'t correct!');
    }
  });
  
  return (
    <Form
      {...layout}
      name="basic"
      initialValues={{
        remember: true,
      }}
      onFinish={login}
      //onFinishFailed={onError}
    >
      <Form.Item
        //label="Email"
        name="email"
        rules={[
          {
            type: 'email',
            message: 'The input is not valid email!',
          },
          {
            required: true,
            message: 'Please input your email!',
          },
        ]}
      >
        <Input prefix={<UserOutlined className="site-form-item-icon" />} placeholder="Email"/>
      </Form.Item>

      <Form.Item
        //label="Password"
        name="password"
        rules={[
          {
            required: true,
            message: 'Please input your password!',
          },
        ]}
      >
        <Input.Password prefix={<LockOutlined className="site-form-item-icon" />} placeholder="Password"/>
      </Form.Item>

      <Form.Item {...tailLayout} name="remember" valuePropName="checked">
        <Checkbox>Remember me</Checkbox>
      </Form.Item>

      <Form.Item {...tailLayout}>
        <Button type="primary" htmlType="submit" className="login-form-button">
          Login
        </Button>
      </Form.Item>
    </Form>
  );
};

const Login = ({ loggingIn, location,...rest}) => {
  let isLoggedIn = Token.tokenRefreshIsValid();
  return(
    isLoggedIn ? <Redirect to={location.state ? location.state.from : '/'} /> :
    <Row justify="space-around" align="middle" style={{ height: '100vh' }}>
      <Col span={20}>
        <Spin spinning={loggingIn} size="large">
          <Title style={{ color: '#333', fontWeight: 'normal', textAlign: 'center' }}>
            Login Form
          </Title>
          <LoginForm {...rest}/>
        </Spin>
      </Col>
    </Row>
  );
}

export { Login };