import { connect } from 'react-redux';
import { roomTypeAction } from '../actions';
import { RoomType } from '../components';

const mapStateToProps = state => {
    return Object.assign({}, state.roomTypes);
}

const mapDisPatchToProps = dispatch => {
    return {
        getRoomType: () => dispatch(roomTypeAction.getRoomType()),
        getRoomTypeById: (id) => dispatch(roomTypeAction.getRoomTypeById(id)),
        createRoomType: (payload) => dispatch(roomTypeAction.createRoomType(payload)),
        editRoomTypeById: (id, payload) => dispatch(roomTypeAction.editRoomTypeById(id, payload)),
        deleteRoomTypeById: (id) => dispatch(roomTypeAction.deleteRoomTypeById(id))
    }
}

const ConnectedRoomTypeList = connect(mapStateToProps, mapDisPatchToProps)(RoomType)

export { ConnectedRoomTypeList as RoomType };