import React from 'react';
import { Link } from 'react-router-dom';
import { Card, PageHeader } from 'antd';
import { 
    UploadOutlined,
    ProfileOutlined,
    GroupOutlined,
    RocketFilled,
    BuildFilled,
    FormatPainterFilled,
    SkinFilled
} from '@ant-design/icons';
const { Meta } = Card;

const routes = [
    {
        path: '/versions/',
        exact: true,
        name: 'Versions',
        icon: <RocketFilled />,
    },
    {
        path: '/addressables/',
        exact: true,
        name: 'Addressables',
        icon: <UploadOutlined />,
    },
    {
        path: '/furnitures/',
        exact: true,
        name: 'Furnitures',
        icon: <BuildFilled />,
    },
    {
      path: '/furniture-types/',
      exact: true,
      name: 'Furniture Types',
      icon: <ProfileOutlined />,
    },
    {
        path: '/furniture-colors/',
        exact: true,
        name: 'Furniture Colors',
        icon: <FormatPainterFilled />,
    },
    {
        path: '/furniture-materials/',
        exact: true,
        name: 'Furniture Materials',
        icon: <SkinFilled />,
    },
    {
        path: '/room-types/',
        exact: true,
        name: 'Room Types',
        icon: <GroupOutlined />,
    },
];
const NarCard = ({
    icon,
    name,
    path,
    color,
}) => {
    return (
        <Link to={path}>
            <Card
                hoverable
                style={{
                    width: '230px', 
                    float: 'left',
                    textAlign: 'center',
                    margin: '12px 0px 12px 24px',
                    color: color
                }}
            >
                <span style={{  fontSize: '100px' }}>{icon}</span>
                <Meta title={name} />
            </Card>
        </Link>
    )
}

export const Home = () => {
    return (
        <div>
        <PageHeader title={'Home'}></PageHeader>
        <div>
            {routes.map((item, index) => (
                <NarCard key={index} {...item} />
            ))}
        </div>
        </div>
    )
}