import { furnitureDataService as DataService } from '../services';

const FURNITURE_FETCHED_ALL = 'FURNITURE_FETCHED_ALL';
const FURNITURE_IS_FETCHING = 'FURNITURE_IS_FETCHING';
const FURNITURE_HAS_ERROR = 'FURNITURE_HAS_ERROR';
const FURNITURE_CREATED = 'FURNITURE_CREATED';
const FURNITURE_EDITED = 'FURNITURE_EDITED';
const FURNITURE_DELETED = 'FURNITURE_DELETED';

export const furnitureActionType = {
    FURNITURE_FETCHED_ALL,
    FURNITURE_IS_FETCHING,
    FURNITURE_HAS_ERROR,
    FURNITURE_CREATED,
    FURNITURE_EDITED,
    FURNITURE_DELETED,
};

export const furnitureAction = {
    fetchAllFurniture,
    createFurniture,
    editFurniture,
    deleteFurniture,
};

const furnitureFetchedAll = (data) => ({
    type: FURNITURE_FETCHED_ALL,
    data
});

const furnitureIsFetching = () => ({
    type: FURNITURE_IS_FETCHING
});

const furnitureCreated = (data) => ({
    type: FURNITURE_CREATED,
    data
});

const furnitureEdited = (data) => ({
    type: FURNITURE_EDITED,
    data
});

const furnitureDeleted = (data) => ({
    type: FURNITURE_DELETED,
    data
});

const furnitureHasError = () => ({
    type: FURNITURE_HAS_ERROR
});

function fetchAllFurniture() {
    return async dispatch => {
        dispatch(furnitureIsFetching());
        try {
            let { data } = await DataService.getAll();
            dispatch(furnitureFetchedAll(data));
        } catch(error) {
            dispatch(furnitureHasError());
            console.log(error);
        }
    }
}

function createFurniture(payload) {
    return dispatch => {
        dispatch(furnitureIsFetching());
        DataService.create(payload)
        .then(response => {
            dispatch(furnitureCreated(response.data));
        })
        .catch(error => {
            dispatch(furnitureHasError());
            console.log(error);
        });
    }
}

function editFurniture(id, payload) {
    return dispatch => {
        dispatch(furnitureIsFetching());
        DataService.update(id, payload)
        .then(response => {
            dispatch(furnitureEdited(response.data));
        })
        .catch(error => {
            dispatch(furnitureHasError());
            console.log(error);
        });
    }
}

function deleteFurniture(id) {
    return dispatch => {
        dispatch(furnitureIsFetching());
        DataService.delete(id)
        .then(response => {
            dispatch(furnitureDeleted({id: id}));
        })
        .catch(error => {
            dispatch(furnitureHasError());
            console.log(error);
        });
    }
}