import { authenticationDataService } from '../services';
import { token as Token } from '../helpers';

const LOGGED_IN = 'LOGGED_IN';
const LOGGED_OUT = 'LOGGED_OUT';
const LOGGING_IN = 'LOGGING_IN';
const LOG_IN_ERROR = 'LOG_IN_ERROR';
const FETCHED_USER_DATA = 'FETCHED_USER_DATA';

export const authenticationActionType = {
    LOGGED_IN,
    LOGGED_OUT,
    LOGGING_IN,
    LOG_IN_ERROR,
    FETCHED_USER_DATA,
};

export const authenticationAction = {
    login,
    logout,
    fetchUserData,
};

const loggedIn = () => ({
    type: LOGGED_IN,
});

const loggedOut = () => ({
    type: LOGGED_OUT,
});

const loggingIn = () => ({
    type: LOGGING_IN,
});

const logInError = () => ({
    type: LOG_IN_ERROR,
});

const fetchedUserData = (auth) => ({
    type: FETCHED_USER_DATA,
    auth
});

function login(payload) {
    return dispatch => {

        let remember = payload.remember;
        dispatch(loggingIn());
        
        authenticationDataService.create(payload)
        .then(response => {
            let token = response.data;

            Token.setToken(token, remember ? 14 : 0);

            dispatch(loggedIn());
        })
        .catch(error => {
            console.error(error.config);
            dispatch(logInError());
        })
    }
};

function logout() {
    return dispatch => {
        if(Token.tokenRefreshIsValid()) {
            let token = Token.getToken();
            authenticationDataService.blacklist({refresh: token.refresh})
            .then(response => {
                
            })
            .catch(error => {

            })
        }
        Token.removeToken();
        dispatch(loggedOut());
    }
}

function fetchUserData() {
    return dispatch => {
        authenticationDataService.getUserInfo()
        .then(response => {
            dispatch(fetchedUserData(response.data));
        })
        .catch(error => {
            console.error(error.config);
        });
    }
};