import { MainLayout } from '../components/main-layout.component';
import { connect } from 'react-redux';
import { authenticationAction as Action} from '../actions';
import { history } from '../helpers';

const mapDispatchToProps = dispatch => {
    return {
        logout: () => {
            dispatch(Action.logout());
            history.push('/login/');
        },
    }
};

const connectedMainLayout = connect(null, mapDispatchToProps)(MainLayout);
export { connectedMainLayout as MainLayout };