import React, { useState, useEffect } from 'react';
import { 
    Spin, 
    PageHeader, 
    Button,
    Tag,
} from 'antd';
import { 
    ReferenceTable, 
    ReferenceForm, 
} from '../components';

const fengshuiElements = [
    {
        value: 1,
        text: 'Kim'
    },
    {
        value: 2,
        text: 'Mc'
    },
    {
        value: 3,
        text: 'Thy'
    },
    {
        value: 4,
        text: 'Ha'
    },
    {
        value: 5,
        text: 'Th'
    }
];

const FurnitureColor = ({
    items,
    isFetching,
    fetchAllFurnitureColor,
    createFurnitureColor,
    editFurnitureColor,
    deleteFurnitureColor,
}) => {

    const dataSource = items.map((item, index) => {
        return Object.assign({}, item, { key: item.id, no: index + 1});
    });

    const [visibleForm, setVisibleForm] = useState(false);
    const [actionForm, setActionForm] = useState({action: '', data: {}});
    const onSubmitForm = ({action, result}) => {
        setVisibleForm(false);
        if(action.type === 'create') {
            createFurnitureColor(result)
        } else if(action.type === 'update') {
            editFurnitureColor(action.data.id, result);
        }
    };

    const columns = [
        {
            title: 'No',
            dataIndex: 'no',
            width: '15%'
        },
        {
            title: 'Name',
            dataIndex: 'name',
            editable: true,
            inputType: 'text',
            width: '20%',
            sorter: (a, b) => a.name.localeCompare(b.name)
        },
        {
            title: 'Color value',
            dataIndex: 'code',
            editable: true,
            inputType: 'text',
            width: '20%',
            render: code => (
                <Tag 
                    color={code}
                    style={{
                        fontSize: '1em',
                        width: '5em',
                    }}
                >
                    {code}
                </Tag>
            )
        },
        {
            title: 'Fengshui Element',
            dataIndex: 'element',
            editable: true,
            selectable: true,
            options: fengshuiElements,
            width: '20%',
            filters: fengshuiElements,
            onFilter: (value, record) => record.element === value,
        },
    ];

    useEffect(() => {
        fetchAllFurnitureColor();
    // eslint-disable-next-line
    }, []);

    return (
        <Spin spinning={isFetching}>
            
            <ReferenceForm 
                title={'Furniture Color'}
                visible={visibleForm}
                columns={columns}
                onSubmit={onSubmitForm}
                onCancel={() => setVisibleForm(false)}
                action={actionForm}
            />
            <PageHeader title={'Furniture Colors'}>
                <Button type="primary" onClick={() => {
                    setActionForm({type: 'create', data: {}})
                    setVisibleForm(true);
                }}
                >New</Button>
            </PageHeader>
            <ReferenceTable 
                dataSource={dataSource} 
                columns={columns} 
                style={{
                    margin: '0 24px'
                }}
                onHandleEdit={(record) => {
                    setActionForm({type: 'update', data: record})
                    setVisibleForm(true);
                }}
                onHandleDetail={(record) => {
                    setActionForm({type: 'display', data: record})
                    setVisibleForm(true);
                }}
                onHandleDelete={(record) => {
                    deleteFurnitureColor(record.id);
                }}
            />
        </Spin>
    );
};

export { FurnitureColor };