import React from 'react';
import { InputNumber, Input, Form, Modal } from 'antd';
import 'antd/dist/antd.css';

const TableFormModal = ({ columns, visible, onOk, onCancel, ...restProps}) => {
    const [form] = Form.useForm();
    const listInputs = columns.map((col, index) => {
        return col.editable ? (
            <Form.Item
                key={index}
                label={col.title}
                name={col.dataIndex}
                style={{}}
                rules={[
                    {
                        required: true,
                        message: `Please Input ${col.title}`
                    }
                ]}
            >
                {col.type === 'number' ? <InputNumber /> : <Input/>}
            </Form.Item>
        ) : null
    });

    const onSubmit = async () => {
        try {
            const data = await form.validateFields();
            form.resetFields();
            onOk(data);
        }
        catch(error) {
            //console.log(error);
        }
    }

    return (
        <Modal
            visible={visible}
            onOk={onSubmit}
            onCancel={() => {
                form.resetFields();
                onCancel()
            }}
            {...restProps}
        >
            <Form form={form}>
                {listInputs}
            </Form>
        </Modal>
    );
};

export { TableFormModal };