import { furnitureTypeDataService as DataService } from '../services';

const FURNITURE_TYPE_FETCHED_ALL = 'FURNITURE_TYPE_FETCHED_ALL';
const FURNITURE_TYPE_IS_FETCHING = 'FURNITURE_TYPE_IS_FETCHING';
const FURNITURE_TYPE_CREATED = 'FURNITURE_TYPE_CREATED';
const FURNITURE_TYPE_EDITED = 'FURNITURE_TYPE_EDITED';
const FURNITURE_TYPE_DELETED = 'FURNITURE_TYPE_DELETED';
const FURNITURE_TYPE_HAS_ERROR = 'FURNITURE_TYPE_HAS_ERROR';

export const furnitureTypeActionType = {
    FURNITURE_TYPE_FETCHED_ALL,
    FURNITURE_TYPE_IS_FETCHING,
    FURNITURE_TYPE_CREATED,
    FURNITURE_TYPE_EDITED,
    FURNITURE_TYPE_DELETED,
    FURNITURE_TYPE_HAS_ERROR,
};

export const furnitureTypeAction = {
    fetchAllFurnitureType,
    createFurnitureType,
    editFurnitureType,
    deleteFurnitureType,
};

const furnitureTypeFetchedAll = (data) => ({
    type: FURNITURE_TYPE_FETCHED_ALL,
    data
});

const furnitureTypeIsFetching = () => ({
    type: FURNITURE_TYPE_IS_FETCHING
});

const furnitureTypeCreated = (data) => ({
    type: FURNITURE_TYPE_CREATED,
    data
});

const furnitureTypeEdited = (data) => ({
    type: FURNITURE_TYPE_EDITED,
    data
});

const furnitureTypeDeleted = (data) => ({
    type: FURNITURE_TYPE_DELETED,
    data
});

const furnitureTypeHasError = (error) => ({
    type: FURNITURE_TYPE_HAS_ERROR,
    error
});

function fetchAllFurnitureType() {
    return async dispatch => {
        dispatch(furnitureTypeIsFetching());
        try {
            let {data} = await DataService.getAll();
            dispatch(furnitureTypeFetchedAll(data));
        } catch(error) {
            dispatch(furnitureTypeHasError());
            console.log(error);
        }
    }
}

function createFurnitureType(payload) {
    return dispatch => {
        dispatch(furnitureTypeIsFetching());
        DataService.create(payload)
        .then(response => {
            dispatch(furnitureTypeCreated(response.data));
        })
        .catch(error => {
            dispatch(furnitureTypeHasError());
            console.log(error);
        });
    }
}

function editFurnitureType(id, payload) {
    return dispatch => {
        dispatch(furnitureTypeIsFetching());
        DataService.update(id, payload)
        .then(response => {
            dispatch(furnitureTypeEdited(response.data));
        })
        .catch(error => {
            dispatch(furnitureTypeHasError());
            console.log(error);
        });
    }
}

function deleteFurnitureType(id) {
    return dispatch => {
        dispatch(furnitureTypeIsFetching());
        DataService.delete(id)
        .then(response => {
            dispatch(furnitureTypeDeleted({id: id}));
        })
        .catch(error => {
            dispatch(furnitureTypeHasError(error));
            console.log(error);
        })
    }
}