import http from '../services/http-common';
import { token as Token} from '../helpers';

const VERSION_FETCHED_ALL = 'VERSION_FETCHED_ALL';
const VERSION_IS_FETCHING = 'VERSION_IS_FETCHING';
const VERSION_HAS_ERROR = 'VERSION_HAS_ERROR';
const VERSION_CREATED = 'VERSION_CREATED';

export const versionActionType = {
    VERSION_FETCHED_ALL,
    VERSION_IS_FETCHING,
    VERSION_HAS_ERROR,
    VERSION_CREATED,
};

export const versionAction = {
    fetchAllVersion,
    createVersion,
};

const versionFetchedAll = (data) => ({
    type: VERSION_FETCHED_ALL,
    data
});

const versionIsFetching = () => ({
    type: VERSION_IS_FETCHING
});

const versionCreated = (data) => ({
    type: VERSION_CREATED,
    data
});

const versionHasError = () => ({
    type: VERSION_HAS_ERROR
});

function fetchAllVersion() {
    return async dispatch => {
        dispatch(versionIsFetching());
        try {
            let { data } = await http.get('/versions/');
            dispatch(versionFetchedAll(data));
        } catch(error) {
            dispatch(versionHasError());
            console.log(error);
        }
    }
}

function createVersion(payload) {
    return dispatch => {
        let options = {
            headers : { 
                "Authorization": "Bearer " + Token.getToken().access,
                "Content-type": "application/json" 
            }
        }
        dispatch(versionIsFetching());
        http.post('/versions/', payload, options)
        .then(response => {
            dispatch(versionCreated(response.data));
        })
        .catch(error => {
            dispatch(versionHasError());
            console.log(error);
        });
    }
}