import { cookie } from './cookie';

const tokenAlias = '_token';
const token = {
    getToken,
    setToken,
    removeToken,
    updateToken,
    tokenAccessIsValid,
    tokenRefreshIsValid,
};

export { token };

function getToken() {
    return JSON.parse(cookie.getCookie(tokenAlias));
}

function setToken(tokenValues, exDays = 0) {
    cookie.setCookie(tokenAlias, JSON.stringify(tokenValues), exDays > 0 ? getExpiredTime(exDays) : false);
}

function updateToken(tokenValues) {
    let oldToken = getToken();
    let newToken = tokenValues;
    if(oldToken != null)
        newToken = Object.assign({}, oldToken, tokenValues);
    cookie.setCookie(tokenAlias, JSON.stringify(newToken), cookie.getExpireCookie(tokenAlias));
}

function removeToken() {
    cookie.removeCookie(tokenAlias);
}

function tokenAccessIsValid() {
    let token = getToken();
    if(token) {
        return timeIsValid(token.expired_access_at)
    }
}

function tokenRefreshIsValid() {
    let token = getToken();
    if(token) {
        return timeIsValid(token.expired_refresh_at)
    }
}

function utc_to_local(utc) {
    let d = new Date(utc - (new Date().getTimezoneOffset() * 60000));
    return d;
}

function timeIsValid(date) {
    let d = utc_to_local(date);
    return d > new Date();
}

function getExpiredTime(exDays) {
    let d = new Date();
    d.setTime(d.getTime() + (exDays*24*60*60*1000));
    return d;
}