import { furnitureMaterialDataService as DataService } from '../services';

const FURNITURE_MATERIAL_FETCHED_ALL = 'FURNITURE_MATERIAL_FETCHED_ALL';
const FURNITURE_MATERIAL_IS_FETCHING = 'FURNITURE_MATERIAL_IS_FETCHING';
const FURNITURE_MATERIAL_HAS_ERROR = 'FURNITURE_MATERIAL_HAS_ERROR';
const FURNITURE_MATERIAL_CREATED = 'FURNITURE_MATERIAL_CREATED';
const FURNITURE_MATERIAL_EDITED = 'FURNITURE_MATERIAL_EDITED';
const FURNITURE_MATERIAL_DELETED = 'FURNITURE_MATERIAL_DELETED';

export const furnitureMaterialActionType = {
    FURNITURE_MATERIAL_FETCHED_ALL,
    FURNITURE_MATERIAL_IS_FETCHING,
    FURNITURE_MATERIAL_HAS_ERROR,
    FURNITURE_MATERIAL_CREATED,
    FURNITURE_MATERIAL_EDITED,
    FURNITURE_MATERIAL_DELETED,
};

export const furnitureMaterialAction = {
    fetchAllFurnitureMaterial,
    createFurnitureMaterial,
    editFurnitureMaterial,
    deleteFurnitureMaterial,
};

const furnitureMaterialFetchedAll = (data) => ({
    type: FURNITURE_MATERIAL_FETCHED_ALL,
    data
});

const furnitureMaterialIsFetching = () => ({
    type: FURNITURE_MATERIAL_IS_FETCHING
});

const furnitureMaterialCreated = (data) => ({
    type: FURNITURE_MATERIAL_CREATED,
    data
});

const furnitureMaterialEdited = (data) => ({
    type: FURNITURE_MATERIAL_EDITED,
    data
});

const furnitureMaterialDeleted = (data) => ({
    type: FURNITURE_MATERIAL_DELETED,
    data
});

const furnitureMaterialHasError = (error) => ({
    type: FURNITURE_MATERIAL_HAS_ERROR,
    error
});

function fetchAllFurnitureMaterial() {
    return async dispatch => {
        dispatch(furnitureMaterialIsFetching());
        try {
            let { data } = await DataService.getAll();
            dispatch(furnitureMaterialFetchedAll(data));
        } catch(error) {
            dispatch(furnitureMaterialHasError());
            console.log(error);
        }
    }
}

function createFurnitureMaterial(payload) {
    return dispatch => {
        dispatch(furnitureMaterialIsFetching());
        DataService.create(payload)
        .then(response => {
            dispatch(furnitureMaterialCreated(response.data));
        })
        .catch(error => {
            dispatch(furnitureMaterialHasError());
            console.log(error);
        });
    }
}

function editFurnitureMaterial(id, payload) {
    return dispatch => {
        dispatch(furnitureMaterialIsFetching());
        DataService.update(id, payload)
        .then(response => {
            dispatch(furnitureMaterialEdited(response.data));
        })
        .catch(error => {
            dispatch(furnitureMaterialHasError());
            console.log(error);
        });
    }
}

function deleteFurnitureMaterial(id) {
    return dispatch => {
        dispatch(furnitureMaterialIsFetching());
        DataService.delete(id)
        .then(response => {
            dispatch(furnitureMaterialDeleted({id: id}));
        })
        .catch(error => {
            dispatch(furnitureMaterialHasError(error));
            //console.log(error);
        });
    }
}