import { connect } from 'react-redux';
import { FurnitureColor } from '../components/furniture-color.component';
import { furnitureColorAction as Action } from '../actions';

const mapStateToProps = state => {
    return Object.assign({}, state.furnitureColors);
};

const mapDispatchToProps = dispatch => {
    return {
        fetchAllFurnitureColor: () => dispatch(Action.fetchAllFurnitureColor()),
        createFurnitureColor: (payload) => dispatch(Action.createFurnitureColor(payload)),
        editFurnitureColor: (id, payload) => dispatch(Action.editFurnitureColor(id, payload)),
        deleteFurnitureColor: (id) => dispatch(Action.deleteFurnitureColor(id)),
    }
};

const connectedFurnitureColor = connect(mapStateToProps, mapDispatchToProps)(FurnitureColor);
export { connectedFurnitureColor as FurnitureColor };