import React from 'react';
import { 
    Button, 
    Space, 
    Table, 
    Popconfirm
} from 'antd';
import { 
    EditOutlined, 
    DeleteOutlined, 
    EyeOutlined, 
    QuestionCircleOutlined
} from '@ant-design/icons';

const ReferenceCell = ({
    children,
    selectable,
    mode,
    select,
    options,
    ...restProps
}) => {
    let childNode = children;
    if(selectable && !mode) {
        let option = options.find(option => option.value === select.value);
        childNode = option ? option.text : null;
    }
    return (
        <td {...restProps}>
            {childNode}
        </td>
    );
};

const ReferenceTable = ({
    dataSource,
    columns,
    onHandleEdit,
    onHandleDetail,
    onHandleDelete,
    ...restProps
}) => {

    const handleDetail = (record) => {
        onHandleDetail(record);
    };

    const handleEdit = (record) => {
        onHandleEdit(record);
    };

    const handleDelete = (record) => {
        onHandleDelete(record);
    };

    const operationColumn = ({
        title: 'Operation',
        dataIndex: 'operation',
        render: (_, record) => {
            return(
                <Space>
                    <Button 
                        onClick={() => handleDetail(record)}
                        title={'View'}
                    >
                        <EyeOutlined style={{color: '#40a9ff'}} />
                    </Button>
                    <Button 
                        title={'Edit'}
                        onClick={() => handleEdit(record)}
                    >
                        <EditOutlined />
                    </Button>
                    <Popconfirm 
                        title={'Are you sure to delete?'}
                        onConfirm={() => handleDelete(record)}
                        okText={'Delete'}
                        okType="danger"
                        icon={<QuestionCircleOutlined style={{color: '#ff0000'}} />}
                    >
                        <Button
                            title={'Delete'}
                        >
                            <DeleteOutlined style={{color: '#ff0000'}} />
                        </Button>
                    </Popconfirm>
                </Space>
            );
        }
    });
    
    const expandedColumns = columns => {
        let newColumns = [...columns];
        newColumns.push(operationColumn);
        return newColumns;
    }

    const mergedColumns = expandedColumns(columns).map((col) => {
        if(!col.selectable) {
            return col;
        }
        
        return {
            ...col,
            onCell: (record) => ({
                selectable: col.selectable,
                select: { value: record[col.dataIndex] },
                options: col.options,
                mode: col.mode,
            })
        }
    });


    return(
        <Table 
            components={{
                body: {
                    cell: ReferenceCell,
                },
            }}
            dataSource={dataSource} 
            columns={mergedColumns}
            {...restProps} 
        />
    )
};

export { ReferenceTable };