import { connect } from 'react-redux';
import { FurnitureMaterial } from '../components/furniture-material.component';
import { furnitureMaterialAction as Action } from '../actions';

const mapStateToProps = state => {
    return Object.assign({}, state.furnitureMaterials);
};

const mapDispatchToProps = dispatch => {
    return {
        fetchAllFurnitureMaterial: () => dispatch(Action.fetchAllFurnitureMaterial()),
        createFurnitureMaterial: (payload) => dispatch(Action.createFurnitureMaterial(payload)),
        editFurnitureMaterial: (id, payload) => dispatch(Action.editFurnitureMaterial(id, payload)),
        deleteFurnitureMaterial: (id) => dispatch(Action.deleteFurnitureMaterial(id)),
    }
};

const connectedFurnitureMaterial = connect(mapStateToProps, mapDispatchToProps)(FurnitureMaterial);
export { connectedFurnitureMaterial as FurnitureMaterial };